.App{
  color: white;
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
h1{
  margin: 0%;
  padding: 0%;
  margin-top: 10px;
}
p{
  margin: 10px;
  font-size: large;
}
button{
 background-color: red;
 border: none;
 padding: 10px 15px;
 border-radius: 7px;
 cursor: pointer;
 color: white;
 margin: 15px;
}
button:hover{
  background-color: black;
  border: 1px solid white;
  
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
